import React, {Component} from 'react'
import { css } from '@emotion/core'
import { Table, Card, Button, Divider, Tag, Tooltip } from 'antd'
import { Link } from 'Config/routes'

class IncomeReport extends Component {
  
  render(){
    const { Column, ColumnGroup } = Table

    const data = [{
      key: '1',
      firstName: 'John',
      lastName: 'Brown',
      age: 32,
      address: 'New York No. 1 Lake Park',
      tags: ['nice', 'developer'],
    }, {
      key: '2',
      firstName: 'Jim',
      lastName: 'Green',
      age: 42,
      address: 'London No. 1 Lake Park',
      tags: ['loser'],
    }, {
      key: '3',
      firstName: 'Joe',
      lastName: 'Black',
      age: 32,
      address: 'Sidney No. 1 Lake Park',
      tags: ['cool', 'teacher'],
    }]

    return(
      <>
      <div><h2>Daftar Karyawan</h2></div>
      <Card css={css`
        border-radius: 8px;
        border-top: 4px solid rgb(24, 144, 255);
        box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
      `}>
        <div css={css`display: flex; justify-content: flex-end; margin-bottom: 1em;`}>
          <Tooltip placement="bottom" title="Export Excel">
            <Button css={css`
            border: 1px solid #ddd;
            &:hover {
              border-color: green;
            }
              img {
                width: 20px;
              }
            `}>
              <img src="/static/icons/excel.png" />
            </Button>
          </Tooltip>
          <Tooltip placement="bottom" title="Print">
            <Button css={css`
            border: 1px solid #ddd;
            &:hover {
              border-color: green;
            }
              img {
                width: 20px;
              }
            `}>
              <img src="/static/icons/print.png" />
            </Button>
          </Tooltip>
          <Link route="addEmployee">
            <Button type="primary">
              Tambah Karyawan
            </Button>
          </Link>
        </div>
        <Table dataSource={data}>
          <ColumnGroup title="Name">
            <Column
              title="First Name"
              dataIndex="firstName"
              key="firstName"
            />
            <Column
              title="Last Name"
              dataIndex="lastName"
              key="lastName"
            />
          </ColumnGroup>
          <Column
            title="Age"
            dataIndex="age"
            key="age"
          />
          <Column
            title="Address"
            dataIndex="address"
            key="address"
          />
          <Column
            title="Tags"
            dataIndex="tags"
            key="tags"
            render={tags => (
              <span>
                {tags.map(tag => <Tag color="blue" key={tag}>{tag}</Tag>)}
              </span>
            )}
          />
          <Column
            title="Action"
            key="action"
            render={(text, record) => (
              <span>
                <a href="javascript:;">Invite {record.lastName}</a>
                <Divider type="vertical" />
                <a href="javascript:;">Delete</a>
              </span>
            )}
          />
        </Table>
      </Card>
      </>
    )
  }
}

export default IncomeReport